import React, {useEffect} from 'react'
import Aos from 'aos';
import "aos/dist/aos.css";
import { GitfinderWrapper, TextShow } from './worksElements'

const Gitfinder = () => {

    useEffect(() => {
        Aos.init({duration: 2000});
      }, [])

    return (
        <>
            <GitfinderWrapper data-aos="fade-right" data-aos-delay="200">
                Gitfinder
                <TextShow to = '/'>Back</TextShow>
            </GitfinderWrapper>       
        </>
    )   
}

export default Gitfinder
